.directions {
  background-color: $special-blue;
}

.directions__wrapper {
  position: relative;
  background-color: $basic-blue-light;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  padding: 0 25px;
  padding-top: 70px;
  padding-bottom: 34px;

  &::before {
    @media (min-width: $tablet-width) {
      content: "";
      position: absolute;
      top: 80px;
      right: 40px;
      width: 288px;
      height: 574px;
      background-image: url("../img/directions/flags-bg.png");
      background-size: 100% 100%;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/directions/flags-bg@2x.png");
      }
    }

    @media (min-width: $desktop-width) {
      top: 88px;
      right: calc(50% - 638px);
    }
  }

  &::after {
    @media (min-width: $tablet-width) {
      content: "";
      position: absolute;
      top: 406px;
      left: calc(50% - 345px);
      width: 141px;
      height: 141px;
      background-color: $special-steel;
      border-radius: 20px;
    }

    @media (min-width: $desktop-width) {
      left: calc(50% - 650px);
      top: 90px;
    }
  }

  @media (min-width: $tablet-width) {
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;

    padding: 73px 75px 80px 73px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;

    padding: 161px 130px 69px 85px;
  }
}

.directions__top {
  padding-left: 25px;
  margin-bottom: 60px;

  @media (min-width: $tablet-width) {
    padding-left: 0;
    margin-bottom: 104px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    margin-left: 164px;
    padding-bottom: 73px;
  }
}

.directions__title {
  @media (min-width: $desktop-width) {
    position: relative;
    z-index: 1;
  }
}

.directions__text {
  font-weight: 300;
  color: $special-grey;
  margin-top: 20px;

  @media (min-width: $tablet-width) {
    position: relative;
    z-index: 1;

    margin-top: 57px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 60px;
  }
}

.directions__list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    position: relative;
    z-index: 1;
  }

  @media (min-width: $desktop-width) {
    order: -1;
  }
}

.directions__item {
  display: flex;
  flex-direction: column;

  width: 270px;
  margin: 0 auto;
  margin-bottom: 1px;

  position: relative;

  &:nth-child(2) {
    margin-bottom: -2px;

    @media (min-width: $tablet-width) {
      margin-bottom: 11px;
    }
  }

  &:last-child {
    @media (min-width: $desktop-width) {
      margin-bottom: 0;
    }
  }

  @media (min-width: $tablet-width) {
    flex-direction: row;
    width: 616px;

    margin-bottom: 11px;
  }

  @media (min-width: $desktop-width) {
    width: 560px;
    margin: 0;
    margin-left: auto;
    margin-bottom: 10px;
  }
}

.directions__img {
  display: block;
  width: 100%;
  height: 110px;
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    position: absolute;
    right: 0;
    top: 0;
    width: 559px;
    height: 140px;
  }

  @media (min-width: $desktop-width) {
    width: 560px;
    right: 8px;
  }
}

.directions__flag {
  content: "";
  position: absolute;
  z-index: 1;

  top: 100px;
  transform: translateY(-50%);
  left: 25px;

  width: 35px;
  height: 24px;
  background-repeat: no-repeat;
  border-radius: 3px;

  @media (min-width: $tablet-width) {
    transform: translateY(0);
    top: 17px;
    right: 150px;
    left: auto;

    width: 70px;
    height: 47px;
  }

  @media (min-width: $desktop-width) {
    top: 23px;
    right: 160px;
  }
}

.directions__link {
  width: 270px;
  padding: 23px 21px 22px 23px;
  margin: 0 auto;
  margin-top: 17px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    width: 618px;
    margin-top: 46px;

    padding: 31px 32px 27px 33px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 2 / 3;
    width: 275px;

    margin: 0;
    margin-left: 164px;
    padding: 30px 33px 27px 33px;

    position: relative;
    z-index: 1;
    transform: translateY(-73px);
  }
}
